*{
  box-sizing:border-box;
  margin:0px;
  padding:0px;
  
}

.clear{
  clear: left;
}
.clear-right{
  clear: right;
}
.top-wrapper{
  width:100%;
  max-width: 1980px;
  margin: 0 auto;
  position: relative;
}
.top-wrapper h3{
  position: absolute;
  bottom:0px;
  right:0px;
}
.top-img{
  width:100%;
}
.top-logo{
  position:absolute;
  top:15%;
  left:1%;
  width: 40%;
}
.header{
  padding:30px 0px;
  width: 100%;
  height: 80px;
  top:0px;
  position: fixed;
  z-index: 10;
  background-color: rgba(0, 183, 255, 0.521);
  color:white;
}
.header ul{
  width: 70%;
  margin:0 auto;
}
.header .list1{
  list-style: none;
  float: left;
  width:25%;
  text-align: center;
  cursor: pointer;
  border-right: 1px solid;
}
.header .list2{
  list-style: none;
  float: left;
  width:25%;
  text-align: center;
  cursor: pointer;
}
.header li p{
  border: none;
  color:white;
  cursor: pointer;
  transition: 0.3s;
}
.header li p:hover{
  color: rgb(0, 0, 0);
}

.content{
  max-width: 1000px;
  width: 80%;
  margin: 0 auto;
  margin-top: 200px;
  margin-bottom: 500px;
 
}

.content h1 {
  position: relative;
  color: #333;
  text-shadow: 0 0 2px white;
  font-size: 50px;
  width:85%;
  max-width: 1000px;
  margin:0 auto;
}

.content h1:before {
  content: "";
  position: absolute;
  background: #9de5ff;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  top: 50%;
  /* border: dashed 1px white; */
  left: -15px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-55%);
  z-index: -1;
}
.clip-box-a {
  position: relative;
  margin: 2em auto;
  padding: 15px 35px 15px 25px;
  width: 90%; /* ボックス幅 */
  background-color: #fff; /* ボックス背景色 */
  color: #555555; /* 文章色 */
  border: 1px solid #ccc; /* 枠線 */
  box-shadow: 1px 1px 1px #acacac; /* 影の色 */
 }
 .clip-box-a::before {
  position: absolute;
  content: '';
  top: -15px;
  right: 10px;
  height: 55px;
  width: 15px;
  border: 3px solid #555555; /*クリップ色 */
  background-color: none;
  border-radius: 12px;
  box-shadow: 1px 1px 2px #999;
  transform: rotate(10deg);
  z-index: 1;
 }
 .clip-box-a::after {
  position: absolute;
  content: '';
  top: 0px;
  width: 10px; 
  right: 20px;
  border: solid 5px #fff; /*背景色と同じ色にする*/
  z-index: 2;
 }
 .clip-box-a button{
  display: block;
  margin:0 auto;
  margin-top:20px;
  margin-bottom: 20px;
  width:300px;
  padding: 5px;
  border:none;
  background-color: rgba(0, 183, 255, 0.973);
  color: white;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.521);
  font-size: 18px;
  font-weight: 800;
  transition: 0.3s;
  border-radius: 30px;
  cursor: pointer;
}
.clip-box-a button:hover{
  background-color: white;
  color:rgba(0, 183, 255, 0.973);
}
.clip-box-a h3{
  text-align: center;
}
.clip-box-a .day{
  margin: 50px 0;
}
